(function() {
  angular.module('interreps').controller('RegisterController', function($rootScope, $scope, $state, $timeout, $interval, $localStorage, FirebaseService, StorageService, toastr, rep, competitions, prices) {
    'ngInject';
    $scope.rep = rep;
    $scope.allCompetitions = competitions;
    $scope.prices = prices;
    $scope.totalCost = $scope.rep.totalCost || 0;
    $scope.selectedPrice = {};
    $scope.img = "../assets/images/" + $scope.rep.user + ".jpg";
    $scope.selectedCompetitions = $scope.rep.competitions || [];
    $scope.user = StorageService.getCurrentUser().user;
    $scope.participants = $scope.rep.participants || [];
    $scope.parts = angular.copy($scope.participants);
    $scope.formInvalid = {
      invalid: false,
      errors: []
    };
    $scope.participant = {};
    $scope.teams = [];
    $scope.team = {
      partitipants: [],
      competition: {}
    };
    $scope.methods = {
      logout: function() {
        StorageService.deleteCurrentUser();
        return $state.go('home', {
          logout: true
        });
      },
      clearTeam: function(item) {
        if (item.team) {
          return item.team = [];
        }
      },
      addParticipant: function() {
        if ($scope.participant !== {}) {
          $scope.participants.push($scope.participant);
          return $scope.participant = {};
        }
      },
      removeParticipant: function(index) {
        return $scope.participants.splice(index, 1);
      },
      clear: function() {
        return $scope.participants = [];
      },
      validateForm: function() {
        var excludes, repAllowedComps;
        $scope.formInvalid.errors = [];
        repAllowedComps = $scope.parts >= 10 ? 3 : 4;
        excludes = _.filter($scope.selectedCompetitions, function(comp) {
          return comp.name === 'Futebol' || comp.name === 'Queimada';
        });
        _.each($scope.parts, function(part) {
          var comps;
          comps = [];
          comps = _.difference(part.competitions, excludes);
          if (comps.length > repAllowedComps) {
            $scope.formInvalid.invalid = true;
            return $scope.formInvalid.errors.push(part.name + " está participando de " + comps.length + " provas. O máximo permitido é " + repAllowedComps);
          }
        });
        if ($scope.formInvalid.invalid) {
          return toastr.error('', '', {
            extraData: {
              formInvalid: $scope.formInvalid
            },
            templates: {
              toast: 'directives/toast/toast.html'
            }
          });
        } else {
          $scope.formInvalid.invalid = false;
          return $scope.methods.save();
        }
      },
      save: function() {
        FirebaseService.updateRepTotalCost($scope.rep.id, $scope.totalCost);
        FirebaseService.updateRepParticipants($scope.rep.id, $scope.participants);
        FirebaseService.updateRepCompetitions($scope.rep.id, $scope.selectedCompetitions);
        return toastr.success('Resgistro salvo com sucesso');
      },
      addCompsToParticipant: function(item) {
        if (item.team) {
          return _.map($scope.parts, function(part) {
            var index, isPartOfTeam, isParticipating;
            part.competitions = part.competitions || [];
            isPartOfTeam = _.find(item.team, function(p) {
              return p.name === part.name;
            });
            isParticipating = _.find(part.competitions, function(c) {
              return c.name === item.name;
            });
            if (isPartOfTeam && !_.find(part.competitions, function(c) {
              return c.name === item.name;
            })) {
              part.competitions.push(item);
            }
            if (!isPartOfTeam && isParticipating) {
              index = part.competitions.indexOf(item);
              return part.competitions.splice(index, 1);
            }
          });
        }
      },
      getTotalPrice: function(newVal) {
        $scope.totalCost = 0;
        return _.each(newVal, function(item) {
          var price;
          price = item.team ? item.price * item.team.length : item.price * 0;
          $scope.totalCost += price;
          return $scope.methods.addCompsToParticipant(item);
        });
      }
    };
    $scope.$watch('selectedCompetitions', function(newVal, oldVal) {
      return $scope.methods.getTotalPrice(newVal);
    }, true);
  });

}).call(this);



