(function() {
  angular.module('interreps').directive("ngBgSlideshow", function($interval) {
    'ngInject';
    return {
      restrict: 'A',
      scope: {
        ngBgSlideshow: '&',
        interval: '='
      },
      templateUrl: 'app/components/bgCarousel/template.html',
      link: function(scope, elem, attr) {
        var change;
        scope.$watch('ngBgSlideshow', function(val) {
          scope.images = val();
          return scope.active_image = 0;
        });
        return change = $interval((function() {
          scope.active_image++;
          if (scope.active_image >= scope.images.length) {
            scope.active_image = 0;
          }
        }), scope.interval || 1000);
      }
    };
  }).controller('MainController', function($rootScope, $scope, $state, $timeout, $interval, $localStorage, FirebaseService, StorageService, toastr, users, reps) {
    'ngInject';
    $scope.users = users;
    $scope.reps = reps;
    $scope.$storage = $localStorage;
    $scope.showForm = false;
    $scope.user = {
      email: void 0,
      password: void 0,
      user: void 0
    };
    $scope.methods = {
      changeView: function() {
        return $scope.showForm = !$scope.showForm;
      },
      login: function() {
        var currentRep, currentUser, user;
        StorageService.deleteCurrentUser();
        currentUser = _.find($scope.users, function(user) {
          return user.user === $scope.user.user && user.password === $scope.user.password;
        });
        currentRep = _.find($scope.reps, function(rep) {
          return rep.user === $scope.user.user && rep.password === $scope.user.password;
        });
        if (currentUser) {
          user = {
            type: 'admin',
            user: currentUser
          };
          StorageService.setCurrentUser(user);
          $timeout(function() {
            return $state.go('admin.reps', {
              id: currentUser.id
            });
          }, 1000);
        }
        if (currentRep) {
          user = {
            type: 'rep',
            user: currentRep
          };
          StorageService.setCurrentUser(user);
          $timeout(function() {
            return $state.go('register', {
              id: currentRep.id
            });
          }, 1000);
        }
        if (!currentUser && !currentRep) {
          return toastr.error('Login e/ou senha inválido(s)');
        }
      }
    };
  });

}).call(this);



